import React from 'react';
import {NavLink, BrowserRouter as Router,Link, Route, Switch} from 'react-router-dom'

class Reset extends React.Component {


    
    state = {
        email: '',        
        message: '',
    
        errors: {
          email: false,          
          
        }
      }

      messages = {
        
        email_incorrect: 'Brak @ w emailu',
        
        
      }
    
      handleChangeLogin = (e) => { 
        const name = e.target.name;
        const type = e.target.type;
        if (type === "text"  || type === "email") {
          const value = e.target.value;
          // const checked = e.target.checked;
          this.setState({
            [name]: value
          })
        } 
      }
    
      handleSubmitPassword = (e) => {
        e.preventDefault()
    
        const validation = this.formValidation()
        // console.log(validation)
    
        if (validation.correct) {
          this.setState({
            
            email: '',
           
           
    
            errors: {
              
              email: false,
              
             
            }
          })
        } else {
          this.setState({
            errors: {
              
              email: !validation.email,
              
              
            }
          })
        }
      }
    
      formValidation() {
     
        
        let email = false;
        
        
        let correct = false;
        const mailReg = new RegExp(/^([a-z0-9_\.-]+)@([\da-z\.-]+)\.([a-z\.]{2,6})$/);
      
    
        if (this.state.email.match(mailReg) ) {
          email = true;
        }
    
     
    
      
    
        if (email) {
          correct = true
          fetch('http://locahost:3000/remember', {
            method: 'POST',
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            },
            body: JSON.stringify({
              "email":this.state.email,
              
              })
          }).then(response=>{
            if(response.ok){           
            
            return response}
                      

                      throw Error ('Błedne dane logowania')
                    }
            
            
            )
            .then(response => response.json())
            .then(data => {
              
              alert(`Hasło zostało zrestartowane i przesłane osobnym mailem`)
            })
            .catch(err => alert("Nie udało się Zrestartować hasła -> Sprawdź email"))
        }
    
        return ({
          correct,         
          email,
        
         
        })
      }
    
     

    render (){
return(
    
    <div className="formularz">
    <span className='titleform'>Reset</span>
    <form onSubmit={this.handleSubmitPassword} noValidate>
      
      <label htmlFor="email">Email:
      <input type="email" id="email" name="email" value={this.state.email} onChange={this.handleChangeLogin} />{this.state.errors.email && <span className='error'>{this.messages.email_incorrect}</span>}<br/>
        
      </label>      
     
      
      <button>Resetuj Hasło</button>
    </form>
    
  </div>











)

    }

}


export default Reset