import React from 'react';
import {NavLink, BrowserRouter as Router,Link, Route, Switch} from 'react-router-dom'
import App from './App'
import Reset from './PrzypomnienieHasła'

class Logowanie extends React.Component {


    
    state = {
        email: '',
        pass: '',
        message: '',
    
        errors: {
          email: false,
          pass: false,
          
        },

        logOn:{
          email: '',
          id:'',
          name:'',

        }

      }

      messages = {
        
        email_incorrect: 'Brak @ w emailu',
        password_incorrect: 'Hasło musi mieć 5 znaków',
        
      }
    
      handleChangeLogin = (e) => { // do zrobienia !!!!!!!!!!!!!!!!!!!!!!!!!!!
        const name = e.target.name;
        const type = e.target.type;
        if (type === "text" || type === "password" || type === "email") {
          const value = e.target.value;
          // const checked = e.target.checked;
          this.setState({
            [name]: value
          })
        } 
      }
    
      handleSubmit = (e) => {
        e.preventDefault()
    
        const validation = this.formValidation()
        // console.log(validation)
    
        if (validation.correct) {
          this.setState({
            
            email: '',
            pass: '',
           
    
            errors: {
              
              email: false,
              pass: false,
             
            }
          })

       





          
        } else {
          this.setState({
            errors: {
              
              email: !validation.email,
              pass: !validation.password,
              
            }
          })
        }





//         fetch('http://localhost:8085/login', {
//   method: 'POST',
//   headers: {
//     'Accept': 'application/json',
//     'Content-Type': 'application/json'
//   },
//   body: JSON.stringify({
//     "email":this.state.email,
//     "pswd":this.state.pass
//     })
// }).then(response=>{return response})
//   .then(response => response.json())
//   .then(data => {
//     this.setState({
//       logOn:{
//           email: data.email,
//           id:data.id,
//           name:data.name_surname,

//       }
//     })
//     alert(`Zalogowany jako ${this.state.logOn.name}`)
//   })

}
  
      
    
      formValidation() {
        // true - ok
        // false - zle
        
        let email = false;
        let password = false;
        
        let correct = false;
        const mailReg = new RegExp(/^([a-z0-9_\.-]+)@([\da-z\.-]+)\.([a-z\.]{2,6})$/);
      
    
        if (this.state.email.match(mailReg) ) {
          email = true;
        }
    
        if (this.state.pass.length > 5) {
          password = true;
        }
    
      
    
        if (email && password) {
          correct = true

          fetch('http://localhost:3000/login', {
            method: 'POST',
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            },
            body: JSON.stringify({
              "email":this.state.email,
              "pswd":this.state.pass
              })
          }).then(response=>{
            if(response.ok){           
            
            return response}
                      

                      throw Error ('Błedne dane logowania')
                    }
            
            
            )
            .then(response => response.json())
            .then(data => {
              this.setState({
                logOn:{
                    email: data.email,
                    id:data.id,
                    name:data.name_surname,
          
                }
              })
              alert(`Zalogowany jako ${this.state.logOn.name}`)
            })
            .catch(err => alert("Nie udało się zalogować"))
        }
    
        return ({
          correct,         
          email,
          password,
         
        })
      }
    
     

    render (){
return(
  <>
  
    <div className="formularz">
    <span className='titleform'>Logowanie</span>
    <form onSubmit={this.handleSubmit} noValidate>
      
      <label htmlFor="email">Email:
      <input type="email" id="email" name="email" value={this.state.email} onChange={this.handleChangeLogin} />{this.state.errors.email && <span className='error'>{this.messages.email_incorrect}</span>}<br/>
        
      </label>

      <label htmlFor="password">Hasło:
      <input type="password" id="password" name="pass" value={this.state.pass} onChange={this.handleChangeLogin} />{this.state.errors.pass && <span className='error'>{this.messages.password_incorrect}</span>}<br/>
        
      </label>
     
      
      <button>Zaloguj</button>
      
    </form>
    <Link to='/reset'>Nie pamiętasz hasła ? </Link>
    
    
    
    
  </div>
  <Router>   
    <Route path='/reset' component={Reset}/>
  </Router>









</>

)

    }

}


export default Logowanie