import React from 'react';
import {NavLink, BrowserRouter as Router,Link, Route, Switch} from 'react-router-dom'

class Rejestracja extends React.Component {

    state = {
        username: '',
        email: '',
        pass: '',
        
        message: '',
    
        errors: {
          username: false,
          email: false,
          pass: false,
          
        }
      }

      messages = {
        username_incorrect: 'Conajmniej 5 znaków',
        email_incorrect: 'Brak @ w emailu',
        password_incorrect: 'Hasło musi mieć 5 znaków',
        
      }
    
      handleChange = (e) => {
        const name = e.target.name;
        const type = e.target.type;
        if (type === "text" || type === "password" || type === "email") {
          const value = e.target.value;
          // const checked = e.target.checked;
          this.setState({
            [name]: value
          })
        } 
      }
    
      handleSubmit = (e) => {
        e.preventDefault()
    
        const validation = this.formValidation()
        // console.log(validation)
    
        if (validation.correct) {
          this.setState({
            username: '',
            email: '',
            pass: '',
            
            message: 'Formularz został wysłany',
    
            errors: {
              username: false,
              email: false,
              pass: false,
              
            }
          })
        } else {
          this.setState({
            errors: {
              username: !validation.username,
              email: !validation.email,
              pass: !validation.password,
              
            }
          })
        }
      }
    
      formValidation() {
        // true - ok
        // false - zle
        let username = false;
        let email = false;
        let password = false;
        
        let correct = false;
        const mailReg = new RegExp(/^([a-z0-9_\.-]+)@([\da-z\.-]+)\.([a-z\.]{2,6})$/);
        
        if (this.state.username.length > 5 && this.state.username.indexOf(' ') === -1) {
          username = true;
        }
    
        if (this.state.email.match(mailReg) ) {
          email = true;
        }
    
        if (this.state.pass.length > 5) {
          password = true;
        }
    
      
    
        if (username && email && password ) {
          correct = true
          fetch('http://localhost:3000/signup', {
            method: 'POST',
            headers: {
              'Accept': 'application/json',
              'Content-Type': 'application/json'
            },
            body: JSON.stringify({
              "name_surname":this.state.username ,
              "email":this.state.email, 
              "pswd": this.state.pass
              
              
              })
          }).then(response=>{
            if(response.ok){           
            
            return response}
                      

                      throw Error ('Błedne dane logowania')
                    }
            
            
            )
            .then(response => response.json())
            .then(data => {
              
              alert(`Dziękuję za rejestrację `)
            })
            .catch(err => alert("Nie udało się ZZarejestrować konta"))
        }
    
        return ({
          correct,
          username,
          email,
          password,
          
        })
      }
    
     

    render (){
return(
    
    <div className="formularz">
    <span className='titleform'>Rejestracja</span>
    <form onSubmit={this.handleSubmit} noValidate>
      <label htmlFor="user"><span className='info'>Login:</span>
      <input type="text" id="user" name="username" value={this.state.username} onChange={this.handleChange} />{this.state.errors.username && <span className='error'>{this.messages.username_incorrect}</span>} <br/>
        
      </label>

      <label htmlFor="email"><span className='info'>Email:</span>
      <input type="email" id="email" name="email" value={this.state.email} onChange={this.handleChange} />{this.state.errors.email && <span className='error'>{this.messages.email_incorrect}</span>}<br/>
        
      </label>

      <label htmlFor="password"><span className='info'>Hasło:</span>
      <input type="password" id="password" name="pass" value={this.state.pass} onChange={this.handleChange} />{this.state.errors.pass && <span className='error'>{this.messages.password_incorrect}</span>}<br/>
        
      </label>
     
      
      <button>Zarejestruj</button>
    </form>
    
  </div>











)

    }


}


export default Rejestracja